*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%; //1rem = 10px  10px/16px = 62.5%

  @include respond(laptop) {
    font-size: 56.25%; //1rem = 9px  9px/16px = 56.25%
  }

  // @include respond(tab-port) {
  //   font-size: 50%; //1rem = 8px  8px/16px = 50%
  // }
}

body {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: $defaul-font-size;
  color: $color-text;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 111rem;
  margin: 0 auto;

  @include respond(desktop-m) {
    padding: 0 2rem;
  }

  &__wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;

    @include respond(laptop) {
      flex-direction: column;
      align-items: center;
    }
  }
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
